
/** 
 ** @desc 数字转字符串并且填充0
 ** @param  {Number} 数字
 ** @param {Number} 长度
 ** @param {String} 填充字符
 ** @return {String}  
 **/
 function beforeFillChar(str,len,char){ 
    if(str.length>len||!len)return str;
    return Array(len-str.length).fill(char||' ').join("")+str;
}
export default beforeFillChar;